<template>
  <div class="container">
    <h1>{{ msg }} Books</h1>
    <p>You have completed <strong>3</strong> books so far, this year.</p>
  </div>
</template>

<script>
export default {
  name: "Completed",
  data() {
    return {
      msg: "Completed"
    };
  }
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
h1,
h2 {
  font-weight: normal;
}
ul {
  list-style-type: none;
  padding: 0;
}
li {
  display: inline-block;
  margin: 0 10px;
}
a {
  color: #42b983;
}
</style>



