<template>
  <div class="container">
    <h1>Reading wishlist</h1>
    <p>{{msg}}</p>
  </div>
</template>

<script>
export default {
  name: "WantToRead",
  data() {
    return {
      msg: "Want to read the books"
    };
  }
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
h1,
h2 {
  font-weight: normal;
}
ul {
  list-style-type: none;
  padding: 0;
}
li {
  display: inline-block;
  margin: 0 10px;
}
a {
  color: #42b983;
}
</style>



